.hero-banner {
    background: url(../../../img/banner/banner.jpg) no-repeat scroll center center;
        background-size: auto;
    position: relative;
    padding: 70px 0;
    background-size: cover;
    min-height: 450px;
}

.hero-banner h2 {
    font-size: 42px;
}

.hero-banner h2 {
    color: #fff;
    text-transform: capitalize;
    margin-bottom: 0;
}
.banner-breadcrumb .breadcrumb-item + .breadcrumb-item::before {
    color: #fff;
    padding-left: .2rem;
    padding-right: .4rem;
}