.latest-blog-area .area-heading {
    margin-bottom: 70px;
}
.area-heading {
    text-align: left;
}
.area-heading p {
    font-size: 14px;
    font-family: "Poppins", sans-serif;
    color: #7f7f7f;
    margin-bottom: 40px;
    line-height: 27px;
    padding-right: 65px;
}
.full_image.single-blog {
    position: relative;
}
.single-blog {
    overflow: hidden;
    margin-bottom: 30px;
}
.single-blog .thumb {
    overflow: hidden;
    position: relative;
}
.img-fluid {
    max-width: 100%;
    height: auto;
}
.single-blog .thumb::after {
    content: '';
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background: #000;
    opacity: 0;
    transition: all 300ms linear 0s;
}
.full_image.single-blog .single-blog-content {
    bottom: 100px;
}

.full_image.single-blog .single-blog-content {
    position: absolute;
    left: 35px;
    bottom: 0;
    opacity: 0;
    visibility: hidden;
    transition: all 300ms linear 0s;
}

.single-blog .tag {
    color: #f84b67;
    text-align: left;
    display: inline-block;
    float: left;
    font-size: 13px;
    font-weight: 300;
    margin-right: 22px;
    position: relative;
}
.single-blog .tag::after {
    content: '';
    position: absolute;
    width: 1px;
    height: 10px;
    background: #acacac;
    right: -12px;
    top: 7px;
}
.single-blog .date {
    color: #fff;;
    text-align: left;
    display: inline-block;
    font-size: 13px;
    font-weight: 300;
}
.full_image.single-blog .date {
    color: #fff;
}
.full_image.single-blog h4 {
    transition: all 300ms linear 0s;
    border-bottom: none;
    padding-bottom: 5px;
}
.single-blog h4 {
    margin-bottom: 25px;
}
h4 {
    font-size: 18px;
}
.full_image.single-blog .single-blog-content .meta-bottom p {
    color: #fff;
}
.single-blog .single-blog-content .meta-bottom p {
    font-size: 13px;
    font-weight: 300;
}
.single-blog .likes {
    margin-right: 16px;
}
.single-blog .single-blog-content .meta-bottom i {
