.about-area {
    position: relative;
}
.area-padding-bottom {
    padding-bottom: 130px;
}
.area-heading {
    margin-bottom: 90px;
    /* text-align: center; */
    margin-top: 15px;
}
.about-area .single-about .about-icon {
    width: 170px;
    height: 95px;
    text-align: center;
    margin-bottom: 20px;
    position: relative;
    z-index: 1;
    line-height: 112px;
}
.about-area .single-about .about-icon i {
    font-size: 40px;
    color: #050817;
}
.area-heading h4 {
    font-size: 45px;
    color: #1d1d1d;
    font-family: "Poppins", sans-serif;
    margin-bottom: 40px;
    line-height: 58px;
}
.area-heading h6 {
    font-size: 16px;
    color: #333333;
    font-weight: 400;
    margin-bottom: 25px;
}
.about-area .single-about {
    text-align: left;
    padding: 40px 0 0;
    transition: all 300ms linear 0s;
}
.about-area .single-about .single-about-content h5 {
    font-size: 20px;
    font-weight: 600;
    color: #1d1d1d;
    margin-bottom: 18px;
}
.about-area::after {
    content: '';
    position: absolute;
    width: 50%;
    height: 100%;
    background-image: url(../../../../img/banner/about1.png);
    background-position: center center;
    background-repeat: no-repeat;
    right: 0;
    top: 0;
}
.about-area .col-lg-6 .single-about .about-icon::after {
    background: #71beb4;
}
.about-area .single-about .about-icon::after {
    content: '';
    position: absolute;
    height: 80px;
    width: 80px;
    background: #fcefed;
    border-radius: 100%;
    left: 0;
    z-index: -1;
    top: 0;
}
