
.single-brand-item {
    cursor: pointer;
    margin-left: 25%;
}
.d-table {
    display: table !important;
}
.d-table-cell {
    display: table-cell !important;
}
.single-brand-item:hover img {
    filter: grayscale(0);
    transition: all 300ms linear 0s;
}
img {
    max-width: 100%;
}
.single-brand-item img {
    filter: grayscale(100%);
    /* transition: all 300ms linear 0s; */
}