.area-padding {
    padding: 130px 0;
}
.pricing_area::before {
    content: '';
    position: absolute;
    width: 100%;
    height: 75%;
    background: #f6f6f6;
        background-repeat: repeat;
        background-image: none;
    background-image: url(../../../../img/background/pattern2.png); 
    background-repeat: repeat-y;
    z-index: -1;
    left: 0;
    top: 0;
}
.pricing_area {
    position: relative;
    z-index: 1;
    padding: 130px 0;
}
.area-padding {
    padding: 130px 0;
}
.pricing_area .area-heading {
    margin-bottom: 70px;
}
.pricing_area .area-heading h4 {
    margin-bottom: 20px;
}

.single-pricing {
    text-align: left;
    padding: 40px 0 45px;
    background: #fff;
    border: 1px solid #edf1f6;
    padding: 40px 30px;
    transition: all 300ms linear 0s;
}
.single-pricing .pricing-icon {
    width: 150px;
    height: 95px;
    text-align: center;
    margin-bottom: 20px;
    position: relative;
    z-index: 1;
    line-height: 112px;
}
.single-pricing .pricing-icon i {
    font-size: 40px;
    color: #050817;
}
.single-pricing {
    text-align: left;
}
.area-heading h4 {
    font-size: 45px;
    color: #1d1d1d;
    font-family: "Poppins", sans-serif;
    margin-bottom: 40px;
    line-height: 58px;
}
.single-pricing .single-pricing-content h5 {
    font-size: 24px;
    font-weight: 600;
    color: #1d1d1d;
    margin-bottom: 18px;
    border-bottom: 1px solid #edf1f6;
    padding-bottom: 12px;
}
.single-pricing .single-pricing-content h4 {
    font-size: 24px;
    font-weight: 600;
    color: #1d1d1d;
    margin-bottom: 46px;
}
.single-pricing .single-pricing-content ul {
    border-bottom: 1px solid #edf1f6;
    padding-bottom: 15px;
    margin-bottom: 20px;
}
ul {
    list-style: none;
    padding: 0;
        padding-bottom: 0px;
}
.single-pricing .single-pricing-content a {
    font-size: 14px;
    color: #1d1d1d;
    border-bottom: 2px solid #edf1f6;
    font-weight: 600;
    padding-bottom: 5px;
}
a {
    text-decoration: none;
    transition: all 0.3s ease-in-out;
    background-color: transparent;
}
.single-pricing .pricing-icon::after {
    background: #71beb4;
}
.pricing_area .col-lg-3 .single-pricing .pricing-icon::after {
    background: #71beb4;
}
.single-pricing .pricing-icon::after {
    content: '';
    position: absolute;
    height: 70px;
    width: 70px;
    background: #fcefed;
    border-radius: 100%;
    left: 0;
    z-index: -1;
    top: 0;
}