.right-button .shop-icon {
    position: relative;
    color: wheat;
}

.right-button ul li {
    display: inline-block;
    margin-left: 68px;
    font-size: 16px;
}
.ti-shopping-cart-full i {
    font-family: 'themify';
    speak: none;
    font-style: normal;
    font-weight: normal;
    font-variant: normal;
    text-transform: none;
    line-height: 1;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

.right-button ul li a {
    color: #fff;
}
.right-button .shop-icon span {
    position: absolute;
    background: #e22104;
    right: 10px;
    top: 10px;
    border-radius: 50px;
    color: #fff;
    width: 15px;
    height: 15px;
    font-size: 10px;
    text-align: center;
    line-height: 14px;
}
.right-button .shop-icon .btn-link:hover {
    
    color: #f84b67;
}
.right-button .shop-icon .btn-link {

    top:20px;
    color: #fff;
    transition: all 0.3s ease-in-out;
}
.right-button .sign_up {
    font-size: 14px;
    color: #222222;
    background: #fff;
    padding: 12px 38px;
    border-radius: 3px;
    font-weight: 500;
}