.over_lay {
    background: url(../../../../img/banner/home-banner.jpg) no-repeat scroll center center;
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    height: 100%;
    bottom: 0;
    z-index: -1;
}

.banner_content{
    color: white;
    margin-top: 100px;
}
.banner_content h3{
    
        font-size: 50px;
        font-family: "Poppins", sans-serif;
        font-weight: 600;
        margin-bottom: 30px;
    }
    .banner_btn {
        display: inline-block;
        background: #f84b67;
        padding: 0 0 0 30px;
        border: 1px solid #f84b67;
        color: #fff;
        font-family: "Poppins", sans-serif;
        font-size: 13px;
        font-weight: 500;
        line-height: 48px;
        outline: none ;
        box-shadow: none ;
        text-align: center;
        cursor: pointer;
        font-family: "Poppins", sans-serif;
        text-transform: uppercase;
        overflow: hidden;
        transition: all 300ms linear 0s;
    }
        .banner_btn Button {
            background: #fff;
            color: #000;
            padding: 18px 19px;
            border-radius: 0 0 0 21px;
            margin-left: 17px;
            transition: all 300ms linear 0s;
        }
    