.area-padding {
    padding: 130px 0;
}.feature-area {
    position: relative;
}
.bg_one {
    background: #2b237c;
}.feature-area .area-heading {
    margin-bottom: 50px;
}
.area-heading {
    text-align: left;
}

.area-heading {
    margin-bottom: 90px;
    text-align: left;
    margin-top: 15px;
}


.area-heading.light h4 {
    color: #fff;
}
.area-heading h4 {
    font-size: 45px;
    color: #1d1d1d;
    font-family: "Poppins", sans-serif;
    margin-bottom: 40px;
    line-height: 58px;
}
.area-heading.light p {
    color: #fff;
}
.area-heading p {
    font-size: 14px;
    font-family: "Poppins", sans-serif;
    margin-bottom: 40px;
    line-height: 27px;
    padding-right: 65px;
}
.feature-area::after {
    content: '';
    position: absolute;
    width: 50%;
    height: 100%;
    background-image: url(../../../../img/banner/about2.png);
    background-position: center left;
    background-repeat: no-repeat;
    left: 165px;
    top: 0;
}
.feature-area .single-feature .feature-icon::after {
    background: #fca6a6;
}
.feature-area .single-feature .feature-icon::after {
    content: '';
    position: absolute;
    height: 80px;
    width: 80px;
    background: #fcefed;
    border-radius: 100%;
    left: 0;
    z-index: -1;
    top: 0;
}
.feature-area .single-feature .feature-icon {
    width: 80px;
    height: 80px;
    text-align: center;
    margin-bottom: 20px;
    position: relative;
    z-index: 1;
    line-height: 100px;
    min-width: 80px;
    margin-right: 40px;
}

.area-heading h4 {
    font-size: 45px;
    color: #1d1d1d;
    font-family: "Poppins", sans-serif;
    margin-bottom: 40px;
    line-height: 58px;
}
.area-heading p {
    font-size: 14px;
    font-family: "Poppins", sans-serif;
    margin-bottom: 40px;
    line-height: 27px;
    padding-right: 65px;
}
.feature-area .single-feature .single-feature-content h5 {
    font-size: 20px;
    font-weight: 600;
    color: #fff;
    margin-bottom: 18px;
}
.feature-area .single-feature .single-feature-content p {
    line-height: 27px;
    font-size: 14px;
    margin-bottom: 35px;
    color: #fff;
}
.feature-area .single-feature .feature-icon i {
    font-size: 40px;
    color: #1d1d1d;
}
