.single-service {
    border: 1px solid #edf1f6;
    background: #fff;
        background-position-x: 0%;
        background-position-y: 0%;
        background-repeat: repeat;
        background-image: none;
    text-align: left;
    padding: 40px 30px 45px;
    background-image: url(../../../../img/elements/single_bg.png);
    background-position: right top;
    background-repeat: no-repeat;
    transition: all 300ms linear 0s;
}
.service-area .single-service .service-icon {
    width: 170px;
    height: 95px;
    text-align: center;
    margin-bottom: 20px;
    position: relative;
    z-index: 1;
    line-height: 112px;
}

.single-service {
    text-align: left;
}
.service-area .single-service .service-icon i {
    font-size: 40px;
    color: #050817;
}


.service-content a {
    font-size: 14px;
    color: #1d1d1d;
    position: relative;
    font-weight: 500;
}
.single-service .service-content a::after {
    content: '';
    position: absolute;
    left: 0;
    bottom: -6px;
    height: 2px;
    width: 90px;
    background: #edf1f6;
}
.area-padding {
    padding: 130px 0;
}
.service-area .single-service .service-content h5 {
    font-size: 20px;
    font-weight: 600;
    color: #1d1d1d;
    margin-bottom: 18px;
}
.service-area .single-service .service-content p {
    line-height: 27px;
    font-size: 14px;
    margin-bottom: 35px;
}
.single-service.mar {
    margin-top: 50px;
}
.col-lg-4 .single-service .service-icon::after {
    background: #fca6a6;
}
.service-area .single-service .service-icon {
    width: 170px;
    height: 95px;
    text-align: center;
    margin-bottom: 20px;
    position: relative;
    z-index: 1;
    line-height: 112px;
}
.service-area .single-service .service-icon::after {
    content: '';
    position: absolute;
    height: 80px;
    width: 80px;
    background: #fcefed;
    border-radius: 100%;
    left: 0;
    z-index: -1;
    top: 0;
}
service-area .single-service .service-icon i {
    font-size: 40px;
    color: #050817;
}