.area-padding-bottom {
    padding-bottom: 130px;
}
.area-heading h4 {
    font-size: 45px;
    color: #1d1d1d;
    font-family: "Poppins", sans-serif;
    margin-bottom: 40px;
    line-height: 58px;
}
.area-heading p {
    font-size: 14px;
    font-family: "Poppins", sans-serif;
    color: #7f7f7f;
    margin-bottom: 40px;
    line-height: 27px;
    padding-right: 65px;
}
.faq_area .card h5 .btn::after {
    content: "\e61a";
    font-family: 'themify';
    font-style: normal;
    font-weight: normal;
    font-variant: normal;
    text-transform: none;
    line-height: 1;
    right: 22px;
    position: absolute;
    top: 19px;
}


.accordion .accordion-item
{
    border: none;
        border-bottom-color: currentcolor;
        border-bottom-style: none;
        border-bottom-width: medium;
    border-radius: 0;
        border-bottom-right-radius: 0px;
        border-bottom-left-radius: 0px;
    margin-bottom: 20px;
}
.accordion .accordion-item .accordion-header .accordion-button.collapsed{
    border: 1px solid #e9ecef;
    background: #f6f6f6;
    font-weight: 600;
    color: #1d1d1d;
    font-size: 16px;
    width: 100%;
    border-radius: 5px;
    text-align: left;
    box-shadow: none;
    transition: all 300ms linear 0s;
}
.accordion .accordion-item .accordion-header .accordion-button {
    font-weight: 600;
    color: #1d1d1d;
    font-size: 16px;
    width: 100%;
    text-align: left;
    padding: 13px 15px;
    box-shadow: 0px 5px 15px 0px rgba(42, 34, 123, 0.1);
    transition: all 300ms linear 0s;
}
.accordion .accordion-item .accordion-header .accordion-button.collapsed:hover {
    text-decoration: none;
}